import { useLayoutEffect } from "react";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";


const ProductInfo = () => {
	gsap.registerPlugin(ScrollTrigger);
	useLayoutEffect(() => {
		let ctx = gsap.context(() => {
			const about = document.querySelectorAll(".zoomer");
			const aboutImg = document.querySelectorAll(".images");

			gsap.from(aboutImg, {
				scrollTrigger: {
					trigger: about,
					start: 'top bottom',
					scrub: 1.1,
					markers: false,
				},
				yPercent: 80,
			});
			gsap.from('.images img', {
				scrollTrigger: {
					trigger: about,
					start: 'top bottom',
					scrub: 1.9
				},
				scale: 1.9
			});
			gsap.to('.info-content', {
				scrollTrigger: {
					trigger: '.infoWrapper',
					start: 'top bottom',
					scrub: 1.9
				},
				yPercent: 50
			})
		});
		return () => ctx.revert();

	}, [])
	return (
		<div className="infoWrapper">
			<div className="info-content">
				<h2 className="p-info-title p-hero-title left">HAMMER
					<p><span className="p-hero-stroke">PRODUCTS</span></p></h2 >
				<p className="p-gallery-desc">
					Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores cumque voluptas nihil! Consectetur, ea. Ratione nam odio mollitia. Mollitia impedit rerum autem quaerat ex fugiat assumenda quam nesciunt recusandae beatae.
				</p></div>
			<div className="zoomer">
				<div className="images">
					<img src={require("../assets/e3.jpg")} alt="test-not" />
				</div>
			</div>
		</div >

	)
}

export default ProductInfo;