import { useEffect, useRef } from "react";
import gsap from "gsap";
import Box from "@mui/material/Box";
import ScrollTrigger from "gsap/all";
gsap.registerPlugin(ScrollTrigger);

const ProductGallery = () => {
	const work = useRef();

	useEffect(() => {

		let ctx = gsap.context(() => {
			console.log(".workItem", document.querySelectorAll(".workItem"));
			gsap.from('.workItem, .workItemNum', {
				y: (i, el) => (1 - parseFloat(el.getAttribute('data-speed'))),
				scrollTrigger: {
					trigger: '.work',
					start: 'top bottom',
					scrub: 1.9,
					// markers: true,
				}
			})
			gsap.from('.workItemImg img', {
				scale: 1.6,
				scrollTrigger: {
					trigger: '.workWrapp',
					start: 'top bottom',
					scrub: 1.9
				}
			})



		}, work);
		return () => ctx.revert();
	}, []);

	return (
		<Box className="work" ref={work} sx={{

			paddingTop: "50px",


			"& .workWrapp": {
				display: "flex",
				flexWrap: "wrap",
				rowGap: "120px",
				justifyContent: "space-between",
				padding: " 0 120px",
			},

			"& .workItem ": {
				position: "relative",
				width: "calc(50% - 60px)",
				height: "100vh",
			},

			"& .workItemNum": {
				position: "absolute",
				display: "inline-block",
				fontSize: "70px",
				color: "#fff",
				mixBlendMode: "difference",
				zIndex: 2,
			},

			"& .workItemImg": {
				width: "100%",
				height: "100%",
				overflow: "hidden",
			},

			"& .workItemImg img": {
				display: "block",
				width: "100%",
				height: "100%",
				objectFit: "cover",
			}
		}}>


			<Box className="workWrapp">
				<Box className="workItem" data-speed="-300">
					<span className="workItemNum" data-speed="-2200">/001</span>
					<Box className="workItemImg">
						<img src={require("../assets/e3.jpg")} alt="3" />
					</Box>
				</Box>
				<Box className="workItem" data-speed="-200">
					<span className="workItemNum" data-speed="-2200">/002</span>
					<Box className="workItemImg">
						<img src={require("../assets/he.jpg")} alt="4" />
					</Box>
				</Box>
				<Box className="workItem" data-speed="-300">
					<span className="workItemNum" data-speed="-3000">/003</span>
					<Box className="workItemImg">
						<img src={require("../assets/e7.jpg")} alt="5" />
					</Box>
				</Box>
				<Box className="workItem" data-speed="-200">
					<span className="workItemNum" data-speed="-3000">/004</span>
					<Box className="workItemImg">
						<img src={require("../assets/e8.jpg")} alt="6" />
					</Box>
				</Box>
			</Box>
		</Box >
	)
}

export default ProductGallery;